.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Open Sans', sans-serif;
}

.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.navbar a {
  text-decoration: none;
  -webkit-transition: all ease .2s;
  -o-transition: all ease .2s;
  transition: all ease .2s;
  color: rgba(64, 106, 214, 0.527);
}

.navbar a:hover {
  color: rgb(64, 107, 214);
  -webkit-transition: all ease .3s;
  -o-transition: all ease .3s;
  transition: all ease .3s;
}

.BookScrollViewContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

button {
  padding: 10px;
  border-radius: 3px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  color: rgba(64, 106, 214, 0.527);
  border: .5px solid rgba(71, 101, 182, 0.356);
}

button:hover {
  color: white;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  background-color: rgb(112, 136, 197);
  border-color: rgba(71, 101, 182, 0.795);
}

.signInAndMenu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  margin-right: 15px;
}

input {
  height: 25px;
  font-size: 15px;
}

.hidden {
  display: none;
}

.delete-btn { 
  background-color: white;
  color: rgba(255, 0, 0, 0.589);
  border: 1px solid rgba(0, 0, 0, 0.322);
  -webkit-transition: .2s all ease;
  -o-transition: .2s all ease;
  transition: .2s all ease;
}

.delete-btn:hover {
  background-color: rgb(235, 70, 70);
  color: white;
  -webkit-transition: .2s all ease;
  -o-transition: .2s all ease;
  transition: .2s all ease;
}

.invalid {
  outline: red 1.5px solid;
}

.sectionContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.AccountBooksStyle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 60%;
}

.addToBookBtn:hover {
  -webkit-transition: .2s all ease;
  -o-transition: .2s all ease;
  transition: .2s all ease;
  background-color: rgb(78, 157, 226);
  color: aliceblue(235, 235, 235)
}

.searchBoxStyle {
  position: relative;
  bottom: 70.5px;
  outline: 1px solid rgba(51, 61, 117, 0.363);
  z-index: 1;
  background-color: white;
}

.fakePrompt {
  width: 80%;
  height: 300px;
  /* border: rgba(0, 0, 0, 0.507) solid 1.5px; */
  margin: 30px;
  border-radius: 6px;
  -webkit-box-shadow: 0 2px 2px rgba(78, 87, 138, 0.616);
          box-shadow: 0 2px 2px rgba(78, 87, 138, 0.616);
  background-color: aliceblue;
}

.textareaStyling {
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid gray;
  width: 80%;
  height: 50%;
  margin-bottom: 8px
}

.arrowStyle {
  font-size: 40px; 
  cursor: pointer;
  color: rgba(64, 106, 214, 0.5);
  -webkit-transition: all ease .2s;
  -o-transition: all ease .2s;
  transition: all ease .2s;
}

.arrowStyle:hover {
  color: rgb(64, 107, 214);
  -webkit-transition: all ease .2s;
  -o-transition: all ease .2s;
  transition: all ease .2s;
}

.bookCoverStyles {
  font-family: 'Dancing Script', cursive;
  cursor: pointer;
  width: 90%;
  height: 240px;
  border-radius: 6px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 20px;
  -webkit-transition: all ease .2s;
  -o-transition: all ease .2s;
  transition: all ease .2s;
  color: rgb(6, 55, 107);
  font-size: 30px;
  background-color: rgba(69, 151, 228, 0.212);
}

.bookCoverStyles:hover {
  -webkit-box-shadow: -2px 2px 5px rgba(113, 152, 189, 0.616);
          box-shadow: -2px 2px 5px rgba(113, 152, 189, 0.616);
  -webkit-transition: all ease .2s;
  -o-transition: all ease .2s;
  transition: all ease .2s;
}


